.services__title {
  font-family: 'Unbounded';
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  margin-bottom: 60px;
}

.services__list {
  display: flex;
  justify-content: center;
  column-gap: 20px;
  margin-bottom: 173px;
}

.services__link {
  display: block;
  max-width: 289px;
  text-decoration: none;
  color: inherit;
}

.services__link_img {
  width: 100%;
  height: 380px;
  margin-bottom: 14px;

}

.services__link_title {
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.02em;
  margin-bottom: 10px;
}

.services__link_desc {
  font-size: 16px;
line-height: 18px;
letter-spacing: 0.02em;
color: #737373;
}