.benefits {
  max-width: 1920px;
  max-height: 560px;
  margin: 0 auto;
  background-image: url('../images/benefits/benefits__bg.jpg');
}

.benefits__wrapper {
  padding: 90px 0 148px;
}

.benefits__title {
  font-family: 'Unbounded';
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  color: #F2F6F1;
  margin-bottom: 60px;
}

.benefits__list {
  display: flex;
  justify-content: flex-start;
  column-gap: 20px;
}

.benefits__item {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 289px;
  height: 230px;
  background-color: #F2F6F1;
  border-radius: 10px;
}

.benefits__item_title {
  max-width: 200px;
  margin: 20px 0 25px 20px;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.02em;
}

.benefits__item_desc {
  margin-left: 20px;
  max-width: 240px;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.02em;
  color: #737373;

}

.benefits__item_number {
  display: block;
  margin-bottom: 16px;
  margin-left: 233px;
  color: #737373;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
}