.footer__wrapper {
  margin-top: 140px;
  margin-bottom: 80px;
}

.footer__wrapper {
  display: flex;
  justify-content: start;
  column-gap: 325px;
}

.footer__contacts {
  max-width: 289px;
}

.footer__contacts_phone {
  display: block;
  text-decoration: none;
  color: inherit;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.02em;
  margin-bottom: 30px;
}

.footer__contacts_address {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 0.02em;
  color: #737373;
}

.footer__nav {
  margin-top: 71px;
  max-width: 602px;
  min-height: 18px;
  background-color: transparent;
  display: flex;
  justify-content: start;
  column-gap: 40px;
}

.footer__nav-link {
  text-decoration: none;
  display: block;
  color: #737373;
  transition: color 0.15s ease-in;
}

.footer__nav-link:hover {
  color: #1A1A1A;
  transition: color 0.3s ease-in;
}

.footer__nav-link.active {
  color: #1A1A1A;
}

