.subheader {
  max-width: 1920px;
  min-height: 740px;
  margin: 0 auto;
  background-image: url('../images/header/header__bg.jpg');
}

.subheader__title {
  display: block;
  color: #F2F6F1;
  padding: 317px 0 80px;
  max-width: 596px;
  font-family: 'Unbounded';
  font-weight: 500;
  font-size: 64px;
  line-height: 64px;
}

.subheader__btn {
  display: inline-block;
  background-color: transparent;
  padding: 10.5px 38.5px;
  margin-bottom: 103px;
  text-decoration: none;
  color: #F2F6F1;
  font-size: 16px;
  letter-spacing: 0.02em;
  border-radius: 7.14em;
  border: 1.5px solid #F2F6F1;
  transition: all 0.3s ease-in-out;
}

.subheader__btn:hover {
  background-color: #F2F6F1;
  color: #1A1A1A;
  transition: all 0.3s ease-in-out;
}