.aboutCompany__wrapper {
  margin: 120px 0 140px;
  display: flex;
  justify-content: space-between;
  column-gap: 80px;
}

.aboutCompany__img {
  max-width: 598px;
}

.aboutCompany__desc-title {
  max-width: 392px;
  font-weight: 400;
  font-size: 16px;
  line-height: 18.4px;
  letter-spacing: 0.02em;
  color: #1A1A1A;
  margin-bottom: 30px;
}

.aboutCompany__desc-subtitle {
  max-width: 392px;
  font-weight: 400;
  font-size: 16px;
  line-height: 18.4px;
  letter-spacing: 0.02em;
  color: #1A1A1A;
  margin-bottom: 100px;
}

.aboutCompany__desc_btn {
  display: inline-block;
  padding: 12px 40px;
  background-color: #737373;
  border-radius: 7.14em;
  text-decoration: none;
  font-size: 16px;
  letter-spacing: 0.02em;
  color: #F2F6F1;
  transition: background-color 0.3s ease-in-out;
}

.aboutCompany__desc_btn:hover {
  background-color: #485838;
  transition: all 0.3s ease-in-out;
}