.copyright {
  max-width: 100%;
  min-height: 100px;
  background-color: #1A1A1A;
}

.copyright__wrapper {
  padding: 43px 0;
  display: flex;
  justify-content: start;
  column-gap: 40px;
}

.copyright__link {
  text-decoration: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #F2F6F1;
  letter-spacing: 0.02em;
  opacity: 0.5;
  transition: opacity 0.15s ease-in-out;
}

.copyright__link:hover {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.copyright__link.active {
  opacity: 1;
}