.header {
  z-index: 5;
  width: 100%;
  min-height: 50px;
  background-color: #1A1A1A;
  position: absolute;
  top: 0;
  left: 0;
}

.header__wrapper {
  min-height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  color: rgba(242, 246, 241, 0.75);
  text-decoration: none;
  letter-spacing: 0.02em;
  transition: color 0.15s ease-in;
}

.header__logo:hover {
  color: rgba(242, 246, 241, 1);
}

.header__logo.active {
  color: rgba(242, 246, 241, 1);
}

.header__nav {
  display: flex;
  justify-content: center;
  column-gap: 50px;
  align-items: center;
}

.header__nav-link {
  color: rgba(242, 246, 241, 0.75);
  text-decoration: none;
  letter-spacing: 0.02em;
  transition: color 0.15s ease-in;
}

.header__nav-link:hover {
  color: rgba(242, 246, 241, 1);
  transition: color 0.3s ease-in;
}

.header__nav-link.active {
  color: rgba(242, 246, 241, 1);
}
