.feedback {
  max-width: 1920px;
  max-height: 500px;
  margin: 0 auto;
  background-image: url('../images/feedback/feedback__bg.jpg');
}

.feedback__wrapper {
  padding-top: 130px;
  padding-bottom: 148px;
  display: flex;
  justify-content: start;
  column-gap: 80px;
  align-items: center;
}

.feedback__form_input {
  display: block;
  width: 342px;
  background: rgba(242, 246, 241, 0.75);
  border-radius: 10px;
  height: 58px;
  padding-left: 50px;
  outline: none;
  margin-bottom: 24px;
  transition: background-color 0.3s ease-in-out;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 0.02em;

}

.feedback__form_input:hover {
  background-color: rgba(242, 246, 241, 0.8);
  transition: background-color 0.3s ease-in-out;
}

.feedback__form_input:focus {
  background: rgba(242, 246, 241, 0.95);
}

.feedback__form_textarea {
  display: block;
  width: 342px;
  height: 58px;
  background: rgba(242, 246, 241, 0.75);
  border-radius: 10px;
  padding-bottom: 20px;
  padding-top: 20px;
  padding-left: 50px;
  outline: none;
  resize: none;
  transition: background-color 0.3s ease-in-out;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 0.02em;
}

.feedback__form_textarea:hover {
  background-color: rgba(242, 246, 241, 0.8);
  transition: background-color 0.3s ease-in-out;
}

.feedback__form_textarea:focus {
  background: rgba(242, 246, 241, 0.95);
}

.feedback__desc_title {
  max-width: 392px;
  color: #F2F6F1;
  font-family: 'Unbounded';
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  margin-bottom: 84px;
}

.feedback__desc_btn {
  display: inline-block;
  background-color: transparent;
  padding: 10.5px 38.5px;
  text-decoration: none;
  color: #F2F6F1;
  font-size: 16px;
  letter-spacing: 0.02em;
  border-radius: 7.14em;
  border: 1.5px solid #F2F6F1;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

.feedback__desc_btn:hover {
  background-color: #F2F6F1;
  color: #1A1A1A;
  transition: all 0.3s ease-in-out;
}

