.materials__wrapper {
  margin-top: 120px;
  margin-bottom: 140px;
}

.materials__title {
  font-family: 'Unbounded';
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  margin-bottom: 60px;
}

.materials__inner {
  display: flex;
  justify-content: start;
  align-items: center;
  column-gap: 80px;
}

.materials__list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 20px;
  align-items: center;
  max-width: 598px;
}

.materials__link {
  width: 186px;
  height: 120px;
  position: relative;
}

.materials__link_img {
  width: 100%;
}

.materials__link_name {
  display: block;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 186px;
  height: 120px;
  background-color: #1A1A1A;
  opacity: 0;
  border-radius: 10px;
  left: 0;
  top: 0;
  transition: opacity 0.3s ease-in-out;
}

.materials__link_name:hover {
  opacity: 0.6;
  transition: opacity 0.5s ease-in-out;
}

.materials__link_name {
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  color: #F2F6F1;

}

.materials__desc_text {
  max-width: 392px;
  margin-bottom: 128px;
}

.materials__desc_btn {
  display: inline-block;
  padding: 12px 40px;
  background-color: #737373;
  border-radius: 7.14em;
  text-decoration: none;
  font-size: 16px;
  letter-spacing: 0.02em;
  color: #F2F6F1;
  transition: background-color 0.3s ease-in-out;
}

.materials__desc_btn:hover {
  background-color: #485838;
  transition: all 0.3s ease-in-out;
}